      dark: true,
      showCode: true,
      content: "",
    };
  },
  watch: {
    dark() {
      this.$vuetify.theme.dark = this.dark;
    },
  },
  mounted() {
    this.setContent(this.originContent);
  },
  methods: {
    setContent(cont) {
      this.content = cont;
    },
    async uploadImage({ file, desc, callback }) {
      alert(
        "Upload must be implemented on the server.\nThis is a sample image."
      );
      callback(sample);
    },
  },
